import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import 'semantic-ui-css/semantic.min.css'

import { getAllGroups } from './reducers/groupListReducer'
import { getGroupDetails } from './reducers/groupReducer'
import GroupView from './components/GroupView'

import { Container, Header, List } from 'semantic-ui-react'

import { Switch, Route, Link, useRouteMatch } from 'react-router-dom'

// webpack-dev-server does not refresh the browser when code is updated
// due to this bug: https://github.com/webpack/webpack-dev-server/issues/2758
// TODO: either upgrade to the webpack beta or wait for bugfix

const App = () => {
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getAllGroups())
  }, [dispatch])

  const groups = useSelector(({ groupList }) => groupList)

  return (
    <Container>
      <Header as='h1'>Group Read</Header>
      <List>
        {groups.map(group => 
          <List.Item key={group.id}>
            <List.Content>
              <List.Header as='p'>
                <Link to={`/group/${group.id}`}>
                  {group.groupName}
                </Link>
              </List.Header>
              <List.Description as='p'>{group.bookName}</List.Description>
            </List.Content>
          </List.Item>
        )}
      </List>

      <Switch>
        <Route path="/group/:id">
          <GroupView />
        </Route>
        {/* <Route path="/">
          <GroupList />
        </Route> */}
      </Switch>

    </Container>
  )
}

export default App